
@keyframes spinAround{
from{transform:rotate(0deg)
}
to{transform:rotate(359deg)
}
}
.app-sidebar{position:fixed;top:50px;left:0;bottom:0;padding:20px;width:180px;min-width:45px;max-height:100vh;height:calc(100% - 50px);z-index:1023;background:#FFF;box-shadow:0 2px 3px rgba(17,17,17,0.1),0 0 0 1px rgba(17,17,17,0.1)
}
.app-sidebar .icon{vertical-align:baseline
}
.app-sidebar .icon.is-angle{position:absolute;right:10px;transition:transform .377s ease
}
.app-sidebar .menu-label{padding-left:5px
}
.app-sidebar .menu-list li a[aria-expanded="true"] .is-angle{transform:rotate(180deg)
}
.app-sidebar .menu-list li a+ul{margin:0 10px 0 15px
}



